@import './colors';

.header {
    display: flex;
    height: 200px;
    justify-content: space-around;
    align-items: center;
    text-transform: uppercase;

    .selected {
        text-decoration: none;
        padding: 10px 15px;
    }

    .sousMenu {
        display: flex;
        float: left;
        // flex-direction: column;
        position: absolute;
        // background-color: black;
        border: 2px solid $color-1;
        // right: 30%; 
        top: 235px;  

        a {
            text-decoration: none;
            font-size: 20px;
            color: $color-2;
            padding: 10px 15px;

            &:hover {
                text-shadow: 4px 3px 6px white;
            }
        }
    }

    .logo {
        img {
            overflow: hidden;
        }
    }

    .navBar {
        ul {
            display: flex;

            .sousMenu {
                animation: 0.3s slideFromBot;
            }

            a {
                color: $color-2;
                font-weight: 800;
                font-size: 30px;

                &:hover {
                    color: $color-1; 
                }
            }

            a.selected {
                color: $color-1;
            }

            a.hoverA {
                text-decoration: none;
                font-size: 30px;
                font-weight: 800;
                color: $color-2;
                padding: 10px 15px;

                &:hover {
                    color: $color-1;
                }
            }
        }
    }
}

@keyframes slideFromBot {
    from {
        transform: translateY(30px);
    }

    to {
        transform: translateY(0px);
    }
}

@media screen and (min-width:767px) and (max-width:1024px) {
    .header {
        height: auto;
        flex-direction: column;
        .logo {
            img {
                height: 30vh;
            }
        }
        .sousMenu {
            top: 456px;
            background-color: #fff;
            border: none;
        }
    }
}

@media (max-width:425px) {
    .header {
        flex-direction: column;

        .sousMenu {
            top: 175px;
            right: 6%;
            border: 1px solid white;
        }

        .navBar {
            ul {
                flex-direction: column;
            }
        }
    }
}

@media (max-width:320px) {
    .header {
        .sousMenu {
            a {
                font-size: 16px;
            }
        }

        .navBar {
            ul {
                a.hoverA {
                    font-size: 18px;
                }

                a.selected {
                    font-size: 18px;
                }
            }
        }
    }
}